<template>
  <div class="nav-item">
    <router-link v-if="!hasSubNav" :to="'/' + baseLink" class="nav-link nav-text">{{ name }}</router-link>
    <div v-else class="nav-dropdown">
      <div class="nav-text" @click="toggleOpen">{{ name }}</div>
      <ul class="nav-dropdown-container" :class={open}>
        <li v-for="(item, index) in items" :key="index">
          <router-link :to="getRouterLink(item.value)" class="nav-link nav-dropdown-link">{{ item.key }}</router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'NavItemComponent',
  props: {
    name: String,
    baseLink: String,
    items: Array
  },
  data: function () {
    return {
      open: false
    }
  },
  computed: {
    hasSubNav: function () {
      return this.items !== null && this.items !== undefined && this.items.length > 0;
    }
  },
  methods: {
    getRouterLink: function (itemLink) {
      return `/${this.baseLink}/${itemLink}`;
    },
    toggleOpen: function () {
      this.open = !this.open;
    }
  },
  watch: {
    '$route': function (from, to) {
      if (from !== to) this.open = false;
    }
  }
}

</script>

<style lang="scss">

.nav-text{
  padding-left: 0.5rem;
}

.nav-item {
  padding: 0.5rem 0;
  & a {
    display: block;
  }

}

.nav-dropdown-container {
  height: 0;
  overflow: hidden;
  transition: transform .25s ease;
  transform-origin: top;
  transform: scaleY(0);
  background-color: #eeeeee;

  & a {
    padding: 0.5rem 0 0.5rem 1.5rem;
  }

  &.open {
    transform: scaleY(1);
    height: auto;
  }
}
</style>
